*{padding:0;margin:0;border:0;font-family:"Microsoft YaHei";outline-color:inherit;outline:none;vertical-align:baseline;word-wrap:break-word;background-repeat:no-repeat;background:transparent;-webkit-tap-highlight-color:rgba(0,0,0,0);}
	
	input[type='button'],input[type='submit']{
		-webkit-touch-callout:none;
		-webkit-user-select:none;
		-khtml-user-select:none;
		-moz-user-select:none;
		-ms-user-select:none;
		user-select:none;
		cursor:pointer
	}
	a{text-decoration:none;color:#333333}
	a:link{text-decoration:none;color:#333333}
	a:visited{text-decoration:none;color:#333333}
	a:hover{text-decoration:none;color:#333333}
	a:active{text-decoration:none;color:#333333}
	ul,li,ol{list-style:none}
	html,body{
		width:100%;
		background: #fff;
		color:#333333;
	}
	html{
		overflow-y:scroll;
		
	}
	.noselect{-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;}
	.pointer{cursor: pointer;}
	h1,h2,h3,h4,h5,h6{font-weight:100;}
	i,em{font-style:normal;}
	input:-webkit-autofill {
	    -webkit-box-shadow: 0 0 0 1000px white inset !important;
	}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{
  -webkit-appearance: none !important;
  margin: 0; 
}
input[type="number"]{
  -moz-appearance:textfield;
}


