.el-cascader {
    display: inline-block;
    position: relative;
}
.el-cascader .el-input, .el-cascader .el-input__inner {
    cursor: pointer;
}
.el-cascader .el-input__icon {
    transition: none;
}
.el-cascader .el-icon-caret-bottom {
    transition: transform .3s;
}
.el-cascader .el-icon-caret-bottom.is-reverse {
    transform: rotateZ(180deg);
}
.el-cascader.is-disabled .el-cascader__label {
    z-index: 2;
    color: #bbb;
}
.el-cascader__label {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    line-height: 34px;
    padding: 0 25px 0 10px;
    color: rgb(31, 61, 48);
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    box-sizing: border-box;
    cursor: pointer;
    font-size: 14px;
    text-align: left;
}
.el-cascader__label span {
    color: rgb(151, 190, 175);
}
.el-cascader--large {
    font-size: 16px;
}
.el-cascader--large .el-cascader__label {
    line-height: 40px;
}
.el-cascader--small {
    font-size: 13px;
}
.el-cascader--small .el-cascader__label {
    line-height: 28px;
}
.el-cascader-menus {
    white-space: nowrap;
    background: #fff;
    position: absolute;
    margin: 5px 0;
    z-index: 2;
    border: solid 1px rgb(209, 229, 221);
    border-radius: 2px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.el-cascader-menu {
    display: inline-block;
    vertical-align: top;
    height: 204px;
    overflow: auto;
    border-right: solid 1px rgb(209, 229, 221);
    background-color: #fff;
    box-sizing: border-box;
    margin: 0;
    padding: 6px 0;
    min-width: 160px;
}
.el-cascader-menu:last-child {
    border-right: 0;
}
.el-cascader-menu__item {
    font-size: 14px;
    padding: 8px 30px 8px 10px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: rgb(72, 106, 93);
    height: 36px;
    line-height: 1.5;
    box-sizing: border-box;
    cursor: pointer;
}
.el-cascader-menu__item:hover {
    background-color: rgb(228, 241, 238);
}
.el-cascader-menu__item.selected {
    color: #fff;
    background-color: #009e4d;
}
.el-cascader-menu__item.selected.hover {
    background-color: rgb(0, 139, 68);
}
.el-cascader-menu__item.is-active {
    color: #fff;
    background-color: #009e4d;
}
.el-cascader-menu__item.is-active:hover {
    background-color: rgb(0, 139, 68);
}
.el-cascader-menu__item.is-disabled {
    color: rgb(191, 217, 206);
    background-color: #fff;
    cursor: not-allowed;
}
.el-cascader-menu__item.is-disabled:hover {
    background-color: #fff;
}
.el-cascader-menu__item__keyword {
    font-weight: 700;
}
.el-cascader-menu__item--extensible {}
.el-cascader-menu__item--extensible:after {
    font-family: 'element-icons';
    content: "\e606";
    font-size: 12px;
    transform: scale(0.8);
    color: rgb(191, 203, 217);
    position: absolute;
    right: 10px;
    margin-top: 1px;
}
.el-cascader-menu--flexible {
    height: auto;
    max-height: 180px;
    overflow: auto;
}
.el-cascader-menu--flexible .el-cascader-menu__item {
    overflow: visible;
}
