.special-site-wrap[data-v-fdaa0e3c]{
    width: 1200px;
    height: auto;
    margin: 0 auto 14px;
    overflow: hidden;
}
.content-left-class[data-v-fdaa0e3c]{
    position: absolute;
    bottom: 15px;
    width: 190px;
    height: 190px;
    left: 15px;
    background: rgba(255,255,255,.9);
}
.content-left-class p[data-v-fdaa0e3c]{
    position: relative;
    width: 100%;
    height: 50px;
    text-align: center;
}
.content-left-class p[data-v-fdaa0e3c]:after{
    position: absolute;
    content: '';
    width: 20px;
    height: 1px;
    background: #333;
    left: 15%;
    top: 50%;
    z-index: 99;
}
.content-left-class p[data-v-fdaa0e3c]:before{
    position: absolute;
    content: '';
    width: 20px;
    height: 1px;
    background: #333;
    right: 15%;
    top: 50%;
    z-index: 99;
}
.content-left-class p span[data-v-fdaa0e3c]{
    position: absolute;
    line-height: 50px;
    font-size: 18px;
    z-index: 999;
    width: 50%;
    left: 25%;
}
.content-left-class>span[data-v-fdaa0e3c]{
    display: block;
    overflow: hidden;
    padding-top: 10px;
}
.content-left-class span a[data-v-fdaa0e3c]{
    float: left;
    width: 80px;
    height: 22px;
    border: 1px solid #9A9A9A;
    border-radius: 40px;
    -moz-border-radius: 40px;
    -webkit-border-radius: 40px;
    color: #333;
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 14px 8px;
}
.special-site-wrap dt[data-v-fdaa0e3c]{
    text-align: center;
    width: 100%;
    clear: both;
    padding: 54px 0 30px 0;
    font-size: 22px;
    color: #3e3a39;
    position: relative;
}
.special-site-wrap dt[data-v-fdaa0e3c]:after{
    position: absolute;
    content: '';
    top: 60%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ebebeb;
    z-index: 98;
}
.special-site-wrap dt span[data-v-fdaa0e3c]{
    padding: 0 50px;
    position: relative;
    background:#fff;
    z-index: 100;
}
.special-site-wrap dd[data-v-fdaa0e3c]{
    float: left;
    width: 228px;
    height: 160px;
    margin-right: 15px;
    cursor: pointer;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    overflow: hidden;
}
.special-site-wrap dd[data-v-fdaa0e3c]:last-child{
    margin-right: 0;
}
.special-site-wrap dd a[data-v-fdaa0e3c]{
    display: block;
    height: 100%;
}
.special-site-wrap dd span[data-v-fdaa0e3c]{
    display: block;
    text-align: center;
    font-size: 20px;
    margin-top: 20px;
}
.special-site-wrap dd>i[data-v-fdaa0e3c]{
    display: block;
    text-align: center;
    font-size: 14px;
    margin-top: 6px;
    color: #d8d8d8;
}
.special-all[data-v-fdaa0e3c]{
    padding: 26px;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    background: #EEE6DB;
}
.special-all div[data-v-fdaa0e3c]{
    width: 100%;
    height: 100%;
    background: #FCFAF8;
    text-align: center;
}
.special-all div p[data-v-fdaa0e3c]{
    font-size: 20px;
    position: relative;
    line-height: 36px;
}
.special-all div p[data-v-fdaa0e3c]:after{
    position: absolute;
    content: '';
    width: 30%;
    height: 2px;
    background-color:  #636363;
    bottom: -14px;
    left: 35%;
}
.special-site-wrap .special-all span[data-v-fdaa0e3c]{
    background: #009e4d;
    width: 80px;
    height: 80px;
    display: inline-block;
    border-radius: 40px;
    -moz-border-radius: 40px;
    -webkit-border-radius: 40px;
    color: #fff;
    font-size: 20px;
    line-height: 20px;
    margin-top: 28px;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    padding: 14px;
}
.special-site-wrap .special-all span i[data-v-fdaa0e3c]{
    line-height: 26px;
}
.fixed_menu[data-v-fdaa0e3c]{
    position: fixed;
    width: 38px;
    left: 50%;
    top: 30%;
    z-index: 9999;
    background: #fff;
    margin-left: -666px;
}
.fixed_menu dd[data-v-fdaa0e3c]{
    width: 38px;
    height: 38px;
    text-align: center;
    padding: 2px;
    border:1px solid #d8d8d8;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 12px;
    border-bottom: 0;
    cursor: pointer;
}
.fixed_menu dd span[data-v-fdaa0e3c]{
    display: none;
    width: 100%;
    height: 100%;
}
.fixed_menu dd[data-v-fdaa0e3c]:hover{
    border-color: #009e4d;
    background: #009e4d;
    color: #fff;
}
.fixed_menu dd:hover i[data-v-fdaa0e3c]{
    display: none;
}
.fixed_menu dd:hover span[data-v-fdaa0e3c]{
    display: block;
}

/*active*/
.fixed_menu dd.active[data-v-fdaa0e3c]{
    border-color: #009e4d;
    background: #009e4d;
    color: #fff;
}
.fixed_menu dd.active i[data-v-fdaa0e3c]{
    display: none;
}
.fixed_menu dd.active span[data-v-fdaa0e3c]{
    display: block;
    width: 100%;
    height: 100%;
}
.fixed_menu dd[data-v-fdaa0e3c]:last-child{
    border-bottom: 1px solid #d8d8d8;
}
.fixed_menu dd i[data-v-fdaa0e3c]{
    font-size: 22px;
    line-height: 35px;
    color: #999;
}



/* 1.*  */
.index-bannder-wrap[data-v-fdaa0e3c]{
  height:auto;
}
.index-bannder-wrap a[data-v-fdaa0e3c]{
  display: block;
  height: 100%;
  text-align: center;
  overflow: hidden;
}
.index-bannder-wrap a img[data-v-fdaa0e3c]{
  width: auto;
  height: 100%;
}
.index-logo-wrap[data-v-fdaa0e3c]{width:100%;height:158px;border-bottom:2px solid #009e4d;
}
.logo-up[data-v-fdaa0e3c]{padding-top:40px;margin:0 auto;
}
.el-carousel__item h3[data-v-fdaa0e3c] {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}
.el-carousel__item[data-v-fdaa0e3c]:nth-child(2n) {
   background-color: #99a9bf;
}
.el-carousel__item[data-v-fdaa0e3c]:nth-child(2n+1) {
   background-color: #d3dce6;
}
.index-center[data-v-fdaa0e3c]{width:1200px;margin:0 auto 50px;min-height:400px;height:auto!important;height:400px;
}
.index-center[data-v-fdaa0e3c]:after{content:".";display:block;height:0;clear:both;overflow:hidden;visibility:hidden;
}
.index-center-list[data-v-fdaa0e3c]{
  width:100%;
  height:auto;
  padding:40px 0 10px 0;
}
.index-center-list header[data-v-fdaa0e3c]{
  padding:6px 0;
  line-height:30px;
  overflow:hidden;
  margin-bottom: 6px;
}
.index-center-list header span[data-v-fdaa0e3c]{
  float: left;
  font-size: 22px;
  font-weight: 900;
  color: #3e3a39;
}
.index-center-list header div[data-v-fdaa0e3c]{
    float: left;
    margin-left: 20px;
    padding-left: 10px;
    border-left: 1px solid #999;
    font-size: 12px;
}
.index-center-list header i[data-v-fdaa0e3c]{
  float:right;
  cursor:pointer;
  font-size: 14px;
}
.list-content[data-v-fdaa0e3c]{
  padding-top:5px;
  overflow:hidden;
  text-align: center;
  height: auto;
}
.list-content-left[data-v-fdaa0e3c]{
    float: left;
    width: 220px;
    height: 600px;
    position: relative;
    cursor: pointer;
}
.list-content-right[data-v-fdaa0e3c]{
    float: left;
    width: 980px;
}
.list-content-right :nth-child(4n) dl[data-v-fdaa0e3c]{
  border-right-color:#ededed;
}
.list-content-right :nth-child(1) dl[data-v-fdaa0e3c],
.list-content-right :nth-child(2) dl[data-v-fdaa0e3c],
.list-content-right :nth-child(3) dl[data-v-fdaa0e3c],
.list-content-right :nth-child(4) dl[data-v-fdaa0e3c]{
  border-top-color:#ededed;
}
.list-content-right :last-child dl[data-v-fdaa0e3c]{
  border-right-color:#ededed;
}
.list-content-right a[data-v-fdaa0e3c]{
  float:left;
  width:25%;
  height: 300px;
  overflow: hidden;
  border:transparent;
}
.list-content dl[data-v-fdaa0e3c]{
  border:1px solid #ebebeb;
  border-right-color:transparent;
  border-top-color:transparent;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
.list-content dl dt[data-v-fdaa0e3c]{
  border-right-color:transparent;
  border-top-color:transparent;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  padding: 12px;
  padding-bottom: 0;
}
/**/
/*.list-content a:hover dl{
  border:1px solid #009e4d;
  box-shadow: 0 0 50px 1px #d1cece;
}*/
.list-content a dl img[data-v-fdaa0e3c]{
  transition: all .3s linear;
}
.list-content a:hover dl img[data-v-fdaa0e3c]{
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}
.list-content dl [data-v-fdaa0e3c]:nth-child(1){
  overflow: hidden;
  height:231px;
}
.list-content dl :nth-child(1) img[data-v-fdaa0e3c]{
  width:100%;
  height:100%;
}
.list-content dl [data-v-fdaa0e3c]:nth-child(2) {
  font-size: 14px;
  padding:10px 15px 4px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.list-content dl [data-v-fdaa0e3c]:nth-child(3){
  line-height: 24px;
  color:#009e4d;
}
.list-content dl :nth-child(3) i[data-v-fdaa0e3c]{
  color:#009e4d;
  font-size:16px;
  line-height:30px;
}
.list-content dl :nth-child(3) span[data-v-fdaa0e3c]{
  color:#009e4d;
  font-size:20px;
  padding: 0;
}
@media screen and (max-width: 1300px) {
.special-site-wrap[data-v-fdaa0e3c]{
        width: 980px;
}
